import React, { useState, useRef, useEffect } from 'react';
//import HomePrice from './components/HomePrice';
import { Button, Container } from '@material-ui/core';
import HomePriceSlider from './components/HomePriceSlider'
import { Grid } from '@material-ui/core';
import { Card } from '@material-ui/core';
import { CardContent } from '@material-ui/core';
import { CardActions } from '@material-ui/core';
import { Paper } from '@material-ui/core';
import { Box } from '@material-ui/core';
import { shadows } from '@mui/system';
import CarPriceSlider from './components/CarPriceSlider';
import CarLeaseSlider from './components/CarLeaseSlider';
import TextField from '@mui/material/TextField';
import Typography from '@material-ui/core/Typography';
import { formatCurrency, GlobalState } from './utils.js'


class App extends React.Component {
  totals = new Map();

  state = {
    total: 0,
  }

  handleCallback = (id, total) => {
    this.totals.set(id, total)
    this.setState({ total: this.calcTotal })
  }

  calcTotal = () => {
    let sum = 0;
    this.totals.forEach((value) => {
      sum += value;
    });
    return sum;
  }

  render() {
    const { total } = this.state;
    return (
      <Container>
        <Box
          boxShadow={1}
          spacing={3}
          style={{ padding: 10 }}
        >
          <HomePriceSlider parentCallback={this.handleCallback}></HomePriceSlider>
        </Box>

        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          style={{ padding: 10 }}
        >
        </Box>


        <Box
          boxShadow={1}
          spacing={3}
          style={{ padding: 10 }}
        >
          <CarPriceSlider parentCallback={this.handleCallback}/>
        </Box>
        <Box
          boxShadow={1}
          spacing={3}
          style={{ padding: 10 }}
        >
          <CarLeaseSlider parentCallback={this.handleCallback}/>
        </Box>
        <Box
          alignItems="center"
          justifyContent="center"
        >
          <Typography align='right'>
            Total Cost: {formatCurrency(this.calcTotal())}
          </Typography>

        </Box>

      </Container>
    )
  }
}

export default App;




