import React, { useState, useRef, useEffect } from 'react';
import { CardContent, CardHeader, Container, Link, Tooltip } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import HomeTwoToneIcon from '@mui/icons-material/HomeTwoTone';
import Slider from '@material-ui/core/Slider'
import Typography from '@material-ui/core/Typography'
import { formatCurrency, GlobalState } from '../utils.js'
import { Card } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import useWindowDimensions from '../windowDimensions'
import DirectionsCarTwoToneIcon from '@mui/icons-material/DirectionsCarTwoTone';
import ExpenseSummaryCard from './ExpenseSummaryCard.js';
import { v4 as uuid } from 'uuid';

const CustomSlider = withStyles({
    thumb: {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        marginTop: -8,
        marginLeft: -12,
        '&:focus, &:hover': {
            boxShadow: '0px 0px 0px 8px rgba(84, 199, 97, 0.16)'
        },
        '&$active': {
            boxShadow: '0px 0px 0px 12px rgba(84, 199, 97, 0.16)'
        }
    },

    valueLabel: {
        left: 'calc(-50% + 4px)'
    },
    track: {
        height: 8,
        borderRadius: 10,
    },
    rail: {
        height: 5,
        borderRadius: 4
    }
})(Slider)


const vehiclePriceMap = [0, 20000, 30000, 40000, 50000, 60000, 70000]
const homePriceMarks = [
    {
        value: 0,
        label: 'No Car',
    },
    {
        value: 1,
        label: '$20,000',
    },
    {
        value: 2,
        label: '$30,000',
    },
    {
        value: 3,
        label: '$40,000',
    },
    {
        value: 4,
        label: '$50,000',
    },
    {
        value: 5,
        label: '$60,000',
    },
    {
        value: 6,
        label: '$70,000',
    },
];

class CarPriceSlider extends React.Component {
    vehicleDefaultValue = 1;

    state = {
        selectedVehiclePrice: vehiclePriceMap[this.vehicleDefaultValue],
        selectedVehicleTerm: this.vehicleDefaultValue,
        total: vehiclePriceMap[this.vehicleDefaultValue],
    }

    constructor(props) {
        super(props);
        this.props = props
        this.updateVehiclePrice(this.vehicleDefaultValue);
        this.updateTerm(this.vehicleDefaultValue);
    }
    id = uuid();

    updateVehiclePrice(carSelection) {
        this.setState({ selectedVehiclePrice: vehiclePriceMap[carSelection] })
        this.setState({ total: this.state.selectedVehiclePrice / this.state.selectedVehicleTerm })
        this.props.parentCallback(this.id, this.state.total)
    }

    updateTerm(termSelection) {
        this.setState({ selectedVehicleTerm: termSelection });
        this.setState({ total: this.state.selectedVehiclePrice / this.state.selectedVehicleTerm })
        this.props.parentCallback(this.id, this.state.total)
    }

    render() {
        const { selectedVehiclePrice } = this.state;
        const { selectedVehicleTerm } = this.state;
        return (
            <Grid container direction="row"
                justifyContent="center"
                alignItems="center">
                <Grid item xs="auto">
                    <CardContent>
                        <DirectionsCarTwoToneIcon sx={{ fontSize: 80 }} />
                    </CardContent>
                </Grid>

                <Grid item xs={8}>
                    <Container>
                        <Container>
                            <CustomSlider
                                onChange={(event, val) => this.updateVehiclePrice(val)}
                                aria-label="Restricted values"
                                defaultValue={this.vehicleDefaultValue}
                                step={null}
                                marks={homePriceMarks}

                                size='small'
                                min="0"
                                max="6"

                            />
                            <Typography style={{ textAlign: "center", textJustify: "end" }}>
                                Car Price
                            </Typography>
                            <CustomSlider
                                onChange={(event, val) => this.updateTerm(val)}
                                aria-label="Temperature"
                                defaultValue={5}
                                defaultValue={this.vehicleDefaultValue}
                                valueLabelDisplay="auto"
                                step={1}
                                marks
                                min={1}
                                max={10}
                                disabled={selectedVehiclePrice > 0 ? false : true}
                            />
                            <Typography style={{ textAlign: "center", textJustify: "end" }}>
                                Years
                            </Typography>
                        </Container>
                    </Container>
                </Grid>

                <Grid item xs="auto">
                    <ExpenseSummaryCard>
                        <CardContent>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                Annual Car Expense
                            </Typography>
                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    Price <span style={{ opacity: 0 }}>b</span>

                                    <span style={{ float: "right" }}>
                                        {formatCurrency(selectedVehiclePrice)}
                                    </span>
                                </p>
                            </Typography>

                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    Amortized Over
                                    <span style={{ opacity: 0 }}>b</span>
                                    <span style={{ float: "right" }}>
                                        {selectedVehicleTerm}
                                    </span>
                                </p>
                            </Typography>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                TOTAL: {formatCurrency(this.state.total)}
                            </Typography>
                        </CardContent>
                    </ExpenseSummaryCard>
                </Grid>
            </Grid>
        )
    }
}

export default CarPriceSlider;
