import React, { useState, useRef, useEffect } from 'react';
import { CardContent, CardHeader, Container } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import HomeTwoToneIcon from '@mui/icons-material/HomeTwoTone';
import Slider from '@material-ui/core/Slider'
import Typography from '@material-ui/core/Typography'
import { formatCurrency, GlobalState } from '../utils.js'
import { Card } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import useWindowDimensions from '../windowDimensions'
import { maxWidth } from '@mui/system';
import ExpenseSummaryCard from './ExpenseSummaryCard.js';
import { v4 as uuid } from 'uuid';

const CustomSlider = withStyles({
    thumb: {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        marginTop: -8,
        marginLeft: -12,
        '&:focus, &:hover': {
            boxShadow: '0px 0px 0px 8px rgba(84, 199, 97, 0.16)'
        },
        '&$active': {
            boxShadow: '0px 0px 0px 12px rgba(84, 199, 97, 0.16)'
        }
    },

    valueLabel: {
        left: 'calc(-50% + 4px)'
    },
    track: {
        height: 8,
        borderRadius: 10,
    },
    rail: {
        height: 5,
        borderRadius: 4
    }
})(Slider)


const homePriceMap = [0, 250000, 500000, 750000, 1000000, 1500000, 2000000]
const homePriceMarks = [
    {
        value: 0,
        label: 'No House',
    },
    {
        value: 1,
        label: '$250,000',
    },
    {
        value: 2,
        label: '$500,000',
    },
    {
        value: 3,
        label: '$750,000',
    },
    {
        value: 4,
        label: '$1,000,000',
    },
    {
        value: 5,
        label: '$1,500,000',
    },
    {
        value: 6,
        label: '$2,000,000',
    }
];

const homeDefaultValue = 2;

class HomePriceSlider extends React.Component {
    selectedHomePrice;
    annualPropertyTax;
    annualMortgage;
    total;
    id = uuid();


    state = {
        total: 0,
    }
    

    constructor(props) {
        super(props);
        this.props = props
        this.update(homeDefaultValue)
    }

    update(houseSelection) {
        this.selectedHomePrice = homePriceMap[houseSelection];
        this.annualPropertyTax = this.selectedHomePrice * .02201;
        this.annualMortgage = (this.selectedHomePrice * 1.4223) / 30;
        this.total = this.annualMortgage + this.annualPropertyTax;
        this.setState({ ["total"]: this.total })
        this.props.parentCallback(this.id,this.total);
    }

    render() {
        return (
            <Grid container direction="row"
                justifyContent="center"
                alignItems="center">
                <Grid item xs="auto">
                    <CardContent>
                        <HomeTwoToneIcon sx={{ fontSize: 80 }} />
                    </CardContent>
                </Grid>

                <Grid item xs={8}>
                    <Container>
                        <Container>
                            <CustomSlider
                                onChange={(event, val) => {
                                    this.update(val)
                                }}
                                aria-label="Restricted values"
                                defaultValue={homeDefaultValue}
                                step={null}
                                marks={homePriceMarks}
                                size='small'
                                min="0"
                                max="6"

                            />
                        </Container>
                        <Typography style={{ textAlign: "center", textJustify: "end" }}>
                            Home Price
                        </Typography>
                    </Container>
                </Grid>

                <Grid item xs="auto">
                    <ExpenseSummaryCard>
                        <CardContent>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                Annual Housing Expenses
                            </Typography>
                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    Mortgage <span style={{ opacity: 0 }}>b</span>
                                    <span style={{ float: "right" }}>
                                        {formatCurrency(this.annualMortgage)}
                                    </span>
                                </p>
                            </Typography>

                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    Property Tax <span style={{ opacity: 0 }}>b</span>
                                    <span style={{ float: "right" }}>
                                        {formatCurrency(this.annualPropertyTax)}
                                    </span>
                                </p>
                            </Typography>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                TOTAL: {formatCurrency(this.total)}
                            </Typography>
                        </CardContent>
                    </ExpenseSummaryCard>
                </Grid>
            </Grid>
        )
    }
}
export default HomePriceSlider;
