import React, { useState, useRef, useEffect } from 'react';
import { CardContent, CardHeader, Container, Link, Tooltip } from '@material-ui/core';
import { Grid } from '@material-ui/core';
import HomeTwoToneIcon from '@mui/icons-material/HomeTwoTone';
import Slider from '@material-ui/core/Slider'
import Typography from '@material-ui/core/Typography'
import { formatCurrency, GlobalState } from '../utils.js'
import { Card } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import useWindowDimensions from '../windowDimensions'
import DirectionsCarTwoToneIcon from '@mui/icons-material/DirectionsCarTwoTone';
import ExpenseSummaryCard from './ExpenseSummaryCard.js';
import { v4 as uuid } from 'uuid';


const CustomSlider = withStyles({
    thumb: {
        height: 24,
        width: 24,
        backgroundColor: '#fff',
        border: '2px solid currentColor',
        marginTop: -8,
        marginLeft: -12,
        '&:focus, &:hover': {
            boxShadow: '0px 0px 0px 8px rgba(84, 199, 97, 0.16)'
        },
        '&$active': {
            boxShadow: '0px 0px 0px 12px rgba(84, 199, 97, 0.16)'
        }
    },

    valueLabel: {
        left: 'calc(-50% + 4px)'
    },
    track: {
        height: 8,
        borderRadius: 10,
    },
    rail: {
        height: 5,
        borderRadius: 4
    }
})(Slider)


const vehiclePriceMap = [0, 300, 400, 500, 600, 700, 800]
const homePriceMarks = [
    {
        value: 0,
        label: 'No Car',
    },
    {
        value: 1,
        label: '$300',
    },
    {
        value: 2,
        label: '$400',
    },
    {
        value: 3,
        label: '$500',
    },
    {
        value: 4,
        label: '$600',
    },
    {
        value: 5,
        label: '$700',
    },
    {
        value: 6,
        label: '$800',
    },
];

class CarLeaseSlider extends React.Component {
    vehicleDefaultValue = 1;
    state = {
        selectedVehiclePrice: vehiclePriceMap[this.vehicleDefaultValue],
        total: vehiclePriceMap[this.vehicleDefaultValue] * 12,
    }

    constructor(props) {
        super(props);
        this.props = props
        this.update(this.vehicleDefaultValue)
    }

    id = uuid();

    update(carSelection) {
        this.setState({ selectedVehiclePrice: vehiclePriceMap[carSelection] })
        const { selectedVehiclePrice } = this.state
        this.setState({ total: selectedVehiclePrice * 12 })
        this.props.parentCallback(this.id, this.state.total)
    }

    render() {
        const { selectedVehiclePrice } = this.state
        const { total } = this.state
        return (
            <Grid container direction="row"
                justifyContent="center"
                alignItems="center">
                <Grid item xs="auto">
                    <CardContent>
                        <DirectionsCarTwoToneIcon sx={{ fontSize: 80 }} />
                    </CardContent>
                </Grid>

                <Grid item xs={8}>
                    <Container>
                        <Container>
                            <CustomSlider
                                onChange={(event, val) => this.update(val)}
                                aria-label="Restricted values"
                                defaultValue={this.vehicleDefaultValue}
                                step={null}
                                marks={homePriceMarks}

                                size='small'
                                min="0"
                                max="6"

                            />
                            <Typography style={{ textAlign: "center", textJustify: "end" }}>
                                Monthly Lease
                            </Typography>
                        </Container>
                    </Container>
                </Grid>

                <Grid item xs="auto">
                    <ExpenseSummaryCard>
                        <CardContent>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                Annual Car Expense
                            </Typography>
                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    Monthly Cost <span style={{ opacity: 0 }}>b</span>
                                    <span style={{ float: "right" }}>
                                        {formatCurrency(selectedVehiclePrice)}
                                    </span>

                                </p>
                            </Typography>
                            <Typography variant="body2">
                                <p style={{ textAlign: "left" }}>
                                    <span style={{ opacity: 0 }}>b</span>
                                    <span style={{ float: "right" }}>

                                    </span>

                                </p>
                            </Typography>
                            <Typography variant="body1" style={{ textAlign: "center" }}>
                                TOTAL: {formatCurrency(total)}
                            </Typography>
                        </CardContent>

                    </ExpenseSummaryCard>
                </Grid>
            </Grid>
        )
    }
}

export default CarLeaseSlider;
